#content {
    z-index: 2;
}

#main {
    position: relative;
    display: flex;
}

#main:after {
    content: "";
    background: url('./baduy.jpeg') no-repeat center center;
    background-size: cover;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: 0.5;
    z-index: 1;
    border-radius: 3px;
} 

